// Rem output with px fallback
@mixin font-size($sizeValue: 1) {
	font-size: ($sizeValue * 16) * 1px;
	font-size: $sizeValue * 1rem;
}

// Center block
@mixin center-block {
	display: block;
	margin-left: auto;
	margin-right: auto;
}
@mixin content {
        display: flex;
        margin-left: auto;
        margin-right: auto;
        width: 1120px;
        position: relative;
        z-index: 1;
}